import md5 from '@/md5.js'
import {
	mapMutations
} from 'vuex'
export default {
	data() {
		return {
			username: '',
			password: '',
		};
	},
	methods: {
		...mapMutations(['login']),
		getapi() {
			var timestamp = Date.parse(new Date());
			var api_key = this.api_key;
			return [timestamp, md5(api_key + timestamp)]; //返回api_key+时间戳md5加密 
		},
		async userregistered() {
			var key = await
			this.getapi();
			uni.request({
				url: this.url_config+'app/account/register',
				method: 'POST',
				data: {
					time: key[0],
					token: key[1],
					username: this.username,
					password: this.password
				},
				success: (res) => {
					console.log(res.data)
					if (res.data.success === 0) {
						uni.showToast({
							title: res.data.msg,
							duration: 2000
						})
						this.password = ''
					} else {
						
						uni.navigateTo({
							url: '../login/login'
						});
						uni.showToast({
							title: "注册成功",
							duration: 2000
						})
					}
				}
			});
		}
	}
}
